@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto&display=swap');

:root {
    --color-action: #947900;
    --color-bg-action: rgba(255, 0, 0, 0.1);
    --color-bg-dark: #222;
    --color-bg-dark-highlight: #343434;
    --font-text: "Georgia", serif;
    --font-sans: "Bobas Neu", sans-serif;
    --color-links: white;
}

html, body {
    background: var(--color-bg-dark);
    margin: 0;
    color: #eee;
    font-family: var(--font-text);
    font-size: 18px;
}

h1, h2 {
    text-align: center;
    font-family: var(--font-sans);
    text-transform: uppercase;
}

.header-title {
    text-align: left;
    font-size: 2rem;
}

h2 {
    font-size: 1.4rem;
}

p {
    line-height: 1.4;
    margin-bottom: 1.6rem;
}

a {
    color: var(--color-links)
}

sup {
    font-size: .8rem;
}

.page-content {
    padding: 2rem 1rem 2rem 1rem;
    overflow: hidden;
    position: relative;
}

.manifesto {
    font-size: 1.2rem;
    margin-bottom: 6rem;
    position: relative;
    z-index: 1;

    h2 {
        text-align: left;
    }

    ul {
        list-style: none;
        padding: 0;
    }

    li {
        line-height: 1.6;
        margin-bottom: 2rem;
    }
}

.block-action {
    position: relative;
    margin-bottom: 6rem;

    .action-order {
        position: absolute;
        left: 0;
        top: -2rem;
        font-size: 8rem;
        font-family: var(--font-sans);
        color: #555;
        z-index: 0;
    }

    h2 {
        position: relative;
        z-index: 1;
    }

    .action-explain {
        position: relative;
        z-index: 1;
    }
}

.separator {
    position: absolute;
    left: -50vw;
    top: 30vh;
    display: block;
    height: 50vh;
    width: 200%;
    transform: rotate(-30deg);
    background: var(--color-bg-action);
    z-index: 0;
}

.action-explain {
    position: relative;
    z-index: 1;
}

.engage-container {
    position: relative;
    z-index: 0;

    &:before {
        content: "";
        position: absolute;
        left: -50vw;
        top: -10vh;
        display: block;
        height: 50vh;
        width: 200%;
        transform: rotate(-30deg);
        background: var(--color-bg-action);
        z-index: 0;
    }

    &:after {
        content: "";
        position: absolute;
        left: -50vw;
        top: 0;
        display: block;
        height: 50vh;
        width: 200%;
        transform: rotate(-30deg);
        background: var(--color-bg-action);
        z-index: 0;
    }
}

.counter {
    font-size: 1.6rem;
    font-family: var(--font-sans);
}

.counter-value {
    display: block;
    font-size: 3rem;
}

.vote-container {
    position: relative;
    z-index: 1;
}

.recaptcha-container {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.vote-form {
    padding: 1rem 0;

    button {
        font-size: 2rem;
        padding: .5rem 1rem;
        border-radius: 10px;
        color: white;
        border: 3px solid var(--color-action);
        background: var(--color-bg-action);
        cursor: pointer;
        opacity: 1;
        transition: opacity 500ms ease-in;

        &:disabled {
            opacity: .2;
            pointer-events: none;
        }
    }
}

.success-msg {
    position: relative;
    z-index: 1;
    padding: 1rem;
    border: 3px solid var(--color-action);
    background: var(--color-bg-action);
}

.avatar-tool {
    position: relative;
    z-index: 1;
    text-align: center;
    margin-top: 5rem;

    &:before {
        content: "";
        position: absolute;
        left: 20vw;
        top: -10vh;
        display: block;
        height: 50vh;
        width: 200%;
        transform: rotate(-30deg);
        background: var(--color-bg-action);
        z-index: 0;
    }

    &:after {
        content: "";
        position: absolute;
        left: -20vw;
        top: 20vh;
        display: block;
        height: 50vh;
        width: 200%;
        transform: rotate(-30deg);
        background: var(--color-bg-action);
        z-index: 0;
    }
}

.uploader {
    height: 33vh;
    background: var(--color-bg-highlight);
    display: flex;
    padding: 2rem;
    position: relative;
    z-index: 1;

    .upload-button {
        flex: 1 1 auto;
        padding: .5rem 1rem;
        border: 5px dashed #444;
        color: white;
        background: #333;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        cursor: pointer;
    }
}

.avatar-renders {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
}

.avatar-renders canvas {
    width: 120px;
    height: 120px;
    pointer-events: none;
}

img {
    display: block;
}

.controls {
    list-style: none;
    padding: 1rem;
    margin: 0;
    text-align: center;

    li {
        margin-bottom: 1rem;
    }

    button {
        border: 1px solid #888;
        border-radius: 5px;
        padding: .25rem .75rem;
        background: #444;
        color: #ddd;
        cursor: pointer;
    }

    .button-primary {

    }

    .button-secondary {
        border: 0;
        background: transparent;
        padding: 0;
        font-size: .8rem;
        color: #aaa;
    }
}

@media only screen and (min-width: 812px) {
    .page-content {
        width: calc(100% - 4rem);
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-flow: row;
        grid-column-gap: 1rem;
        padding: 0 2rem;
    }

    .header-title {
        grid-column: 1 / 5;
        font-size: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40vh;
        text-align: center;
        margin: 0;
    }

    .manifesto {
        grid-column: 2 / 5;

        ul {
            li {

            }
        }

        .manifesto-bridge {

        }
    }

    .block-action {
        grid-column: 1 / 4;
        text-align: center;
        font-size: 1.4rem;
    }

    .action-second {
        grid-column: 2 / 5;
    }

    .avatar-tool {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .footer {
        grid-column: 1 / 6;
    }
}
