@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto&display=swap";
:root {
  --color-action: #947900;
  --color-bg-action: #ff00001a;
  --color-bg-dark: #222;
  --color-bg-dark-highlight: #343434;
  --font-text: "Georgia", serif;
  --font-sans: "Bobas Neu", sans-serif;
  --color-links: white;
}

html, body {
  background: var(--color-bg-dark);
  color: #eee;
  font-family: var(--font-text);
  margin: 0;
  font-size: 18px;
}

h1, h2 {
  text-align: center;
  font-family: var(--font-sans);
  text-transform: uppercase;
}

.header-title {
  text-align: left;
  font-size: 2rem;
}

h2 {
  font-size: 1.4rem;
}

p {
  margin-bottom: 1.6rem;
  line-height: 1.4;
}

a {
  color: var(--color-links);
}

sup {
  font-size: .8rem;
}

.page-content {
  padding: 2rem 1rem;
  position: relative;
  overflow: hidden;
}

.manifesto {
  z-index: 1;
  margin-bottom: 6rem;
  font-size: 1.2rem;
  position: relative;
}

.manifesto h2 {
  text-align: left;
}

.manifesto ul {
  padding: 0;
  list-style: none;
}

.manifesto li {
  margin-bottom: 2rem;
  line-height: 1.6;
}

.block-action {
  margin-bottom: 6rem;
  position: relative;
}

.block-action .action-order {
  font-size: 8rem;
  font-family: var(--font-sans);
  color: #555;
  z-index: 0;
  position: absolute;
  top: -2rem;
  left: 0;
}

.block-action h2, .block-action .action-explain {
  z-index: 1;
  position: relative;
}

.separator {
  height: 50vh;
  width: 200%;
  background: var(--color-bg-action);
  z-index: 0;
  display: block;
  position: absolute;
  top: 30vh;
  left: -50vw;
  transform: rotate(-30deg);
}

.action-explain {
  z-index: 1;
  position: relative;
}

.engage-container {
  z-index: 0;
  position: relative;
}

.engage-container:before {
  content: "";
  height: 50vh;
  width: 200%;
  background: var(--color-bg-action);
  z-index: 0;
  display: block;
  position: absolute;
  top: -10vh;
  left: -50vw;
  transform: rotate(-30deg);
}

.engage-container:after {
  content: "";
  height: 50vh;
  width: 200%;
  background: var(--color-bg-action);
  z-index: 0;
  display: block;
  position: absolute;
  top: 0;
  left: -50vw;
  transform: rotate(-30deg);
}

.counter {
  font-size: 1.6rem;
  font-family: var(--font-sans);
}

.counter-value {
  font-size: 3rem;
  display: block;
}

.vote-container {
  z-index: 1;
  position: relative;
}

.recaptcha-container {
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  display: flex;
}

.vote-form {
  padding: 1rem 0;
}

.vote-form button {
  color: #fff;
  border: 3px solid var(--color-action);
  background: var(--color-bg-action);
  cursor: pointer;
  opacity: 1;
  border-radius: 10px;
  padding: .5rem 1rem;
  font-size: 2rem;
  transition: opacity .5s ease-in;
}

.vote-form button:disabled {
  opacity: .2;
  pointer-events: none;
}

.success-msg {
  z-index: 1;
  border: 3px solid var(--color-action);
  background: var(--color-bg-action);
  padding: 1rem;
  position: relative;
}

.avatar-tool {
  z-index: 1;
  text-align: center;
  margin-top: 5rem;
  position: relative;
}

.avatar-tool:before {
  content: "";
  height: 50vh;
  width: 200%;
  background: var(--color-bg-action);
  z-index: 0;
  display: block;
  position: absolute;
  top: -10vh;
  left: 20vw;
  transform: rotate(-30deg);
}

.avatar-tool:after {
  content: "";
  height: 50vh;
  width: 200%;
  background: var(--color-bg-action);
  z-index: 0;
  display: block;
  position: absolute;
  top: 20vh;
  left: -20vw;
  transform: rotate(-30deg);
}

.uploader {
  height: 33vh;
  background: var(--color-bg-highlight);
  z-index: 1;
  padding: 2rem;
  display: flex;
  position: relative;
}

.uploader .upload-button {
  color: #fff;
  cursor: pointer;
  background: #333;
  border: 5px dashed #444;
  border-radius: 10px;
  flex: auto;
  justify-content: center;
  align-items: center;
  padding: .5rem 1rem;
  display: flex;
}

.avatar-renders {
  justify-content: center;
  padding-top: 1rem;
  display: flex;
}

.avatar-renders canvas {
  width: 120px;
  height: 120px;
  pointer-events: none;
}

img {
  display: block;
}

.controls {
  text-align: center;
  margin: 0;
  padding: 1rem;
  list-style: none;
}

.controls li {
  margin-bottom: 1rem;
}

.controls button {
  color: #ddd;
  cursor: pointer;
  background: #444;
  border: 1px solid #888;
  border-radius: 5px;
  padding: .25rem .75rem;
}

.controls .button-secondary {
  color: #aaa;
  background: none;
  border: 0;
  padding: 0;
  font-size: .8rem;
}

@media only screen and (min-width: 812px) {
  .page-content {
    width: calc(100% - 4rem);
    grid-column-gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-flow: row;
    padding: 0 2rem;
    display: grid;
  }

  .header-title {
    height: 40vh;
    text-align: center;
    grid-column: 1 / 5;
    justify-content: center;
    align-items: center;
    margin: 0;
    font-size: 4rem;
    display: flex;
  }

  .manifesto {
    grid-column: 2 / 5;
  }

  .block-action {
    text-align: center;
    grid-column: 1 / 4;
    font-size: 1.4rem;
  }

  .action-second {
    grid-column: 2 / 5;
  }

  .avatar-tool {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .footer {
    grid-column: 1 / 6;
  }
}

